const contentSliderData = [
	{
		mainImage: 'http://placekitten.com/300',
	},
	{
		mainImage: 'http://placekitten.com/350',
	},
];

function sliderCreate(elId) {
	new Vue({
		el: `#${elId}`,
		name: elId,
		data: {
			activeSlideNum: 0,
			autoPlayTime: 0,
			autoPlayProgress: 0,
			animation: false,
			autoplayInterval: null,
			autoplay: false,
		},
		beforeCreate() {
			this.autoplayMaxTime = 5000;
			this.slides = (elId === 'heroSlider') ? heroSliderData : contentSliderData;
		},
		methods: {
			selectSlide(slideIndex) {
				if (slideIndex === this.activeSlideNum) return;

				const animationTime = 1400;

				if (this.animation) {
					this.$refs.slide.classList.remove('slider-item--changing-slide');
					this.activeSlideNum = slideIndex;
					this.animation = false;
				} else {
					this.animation = true;
					this.$refs.slide.classList.add('slider-item--changing-slide');

					setTimeout(() => {
						this.activeSlideNum = slideIndex;
						if (this.autoplay) {
							this.autoplayStart();
						}
						this.$nextTick(() => {
							this.paginationScroll();
						});
					}, animationTime / 2);

					setTimeout(() => {
						this.$refs.slide.classList.remove('slider-item--changing-slide');
						this.animation = false;
					}, animationTime);
				}
			},
			paginationScroll() {
				const paginationItemActive = this.$refs.pagination.querySelector('.slider-pagination-item--active');
				const paginationItemActiveHalfWidth = paginationItemActive.offsetWidth / 2;
				const paginationItemActiveScrollLeft = paginationItemActive.offsetLeft + paginationItemActiveHalfWidth;
				const paginationHalfWidth = this.$refs.pagination.offsetWidth / 2;
				const toScroll = (paginationHalfWidth - paginationItemActiveScrollLeft) * -1;

				this.$refs.pagination.scrollLeft = toScroll;
			},
			autoplayStart() {
				const intervalSize = 300;

				this.autoPlayProgress = 0;
				this.autoPlayTime = 0;

				if (this.autoplayInterval) {
					clearInterval(this.autoplayInterval);
				}

				this.autoplayInterval = setInterval(() => {
					this.autoPlayTime += intervalSize;
					this.autoPlayProgress = Math.floor((this.autoPlayTime / this.autoplayMaxTime) * 100);

					// console.log(this.autoPlayProgress);
					if (this.autoPlayProgress >= 100) {
						this.autoPlayProgress = 0;
						clearInterval(this.autoplayInterval);
						this.toNextSlide();
					}
				}, intervalSize);
			},
			toNextSlide() {
				const nextSlideIndex = (this.slides[this.activeSlideNum + 1]) ? this.activeSlideNum + 1 : 0;

				this.selectSlide(nextSlideIndex);
			},
		},
		computed: {
			activeSlide() {
				return this.slides[this.activeSlideNum];
			},
		},
		mounted() {
			if (this.autoplay) {
				this.autoplayStart();
			}
		},
	});
}

const sliders = document.querySelectorAll('.slider');

sliders.forEach((slider) => {
	sliderCreate(slider.id);
});
